import maskLayer from "../../components/common/mask-layer.vue"
export default {
	data() {
		return {
			showTip:false,
			showMask:false

		}
	},
	
	methods: {
		
		getuserinfo(e){
			let {userInfo} = e.detail
			userInfo.like = []
			wx.cloud.callFunction({ // 调用云函数读取用户信息
			  // 要调用的云函数名称
			  name: 'login',
			  success: res => { //利用assign 将两个对象合并起来
				userInfo = Object.assign(userInfo,res.result.event.userInfo)	 
				getApp().globalData.userInfo  = userInfo
				getApp().globalData.playInfo = [] 
				uni.setStorage({
				    key: 'userInfo',
					data: userInfo ,
				    success: function (res) {		
				      // console.log(res);
				    }
				});
			  },
			  fail: err => {
				uni.showModal({
					title:'获取用户信息失败'
				})
			  }
			})	
			this.showTip = false
			this.showMask = !this.showMask
		},
	},
	onReady() {
		let userInfo = getApp().globalData.userInfo
		if(!userInfo) {
			this.$nextTick(()=>{
				this.showTip = true
				this.showMask = true				
			})
		}
	},
	components:{
		maskLayer,
	}
}
